    /*
        POST, GET, PUT calls defined
     */

    function postCall(url, mydata) {
        return new Promise((resolve, reject) => {
            let callingUrl = baseUrl + url;
            // resolve(axios.post(callingUrl, mydata)
            //     .then(response => {
            //         return response;
            //     }));
            axios.post(callingUrl, mydata)
                .then(response => {
                    if (response.status === 200 || response.status === 201) {

                        resolve(response);
                    }
                    else if (response.status === 500) {
                        console.log("error calling api post call");
                        reject();
                    }
                    else {
                        console.log("unexpected error code");
                        reject();
                    }
                }
                )
                .catch(err => {
                    if (err.response.status === 401) {
                        reject(err);
                    }
                    else if (err.response.status === 500) {
                        reject(err)
                    }
                    else {

                    }
                });
        });
    }


    function authenticatedGetCall(url, header) {
        const requestHeader = { headers: { "Authorization": "Bearer " + apiConfig.authenticationToken } };

        return new Promise((resolve, reject) => {
            let callingUrl = baseUrl + url;
            resolve(axios.get(callingUrl, requestHeader)
                .then(response => {
                    return response;
                }));
        });
    }


    function putCall(url, updateOps) {
        return new Promise((resolve, reject) => {
            let callingUrl = baseUrl + url;
            resolve(axios.put(callingUrl, updateOps)
                .then(response => {
                    return response;
                }));
        });
    }